/* 
 * Main file
 */

@import "variables";
@import "elements";

/* Master Styles */
body {
    margin: 0 0 0 60px;
    padding: 0;
}

#map {
    width: 100%;
    height: 800px;
}

.dashboard {
    position: fixed;
    top: 0;
    left: 0;
    bottom: 0;
    height: 100%;
    width: 60px;
    background: $dark-gray;
    z-index: 1000;
}

.dashboard .profile .avatar {
    height: 40px;
    width: 40px;
    border-radius: 40px;
    position: relative;
    -webkit-background-size: cover;
    background-size: cover;
}

.dashboard .profile .avatar:before {
    content: "";
    display: block;
    height: 5px;
    width: 5px;
    top: 0;
    right: 0;
    background-color: $dark-gray;
}

.dashboard .profile .avatar:before {
    content: "";
    display: block;
    height: 4px;
    width: 4px;
    top: 1px;
    right: 1px;
}